import { FC, useState } from "react";
import { FaCheckCircle } from "react-icons/fa";

type Props = {
  image: string;
  title: string;
  period: string;
  pricing: number;
};

const Beneficios = [
  "Membresía",
  "Academia de Cryptos",
  "Ideas de trading",
  "Clases en vivo",
  "Embudo de ventas",
  "Curso de MKT Digita",
  "Curso de tokenomics y gestión de riesgo",
  "Acceso a convención nacional",
];

const Package: FC<Props> = ({ ...props }) => {
  const [loading, setLoading] = useState(false);

  const _createPaymentLink = () => {};
  return (
    <div className="border border-1 relative rounded-lg border-[#3f3f3f] flex flex-col justify">
      <img
        src={props?.image || "/img/but-membership.png"}
        alt={props?.title}
        className="rounded-t-lg image-gradient"
        style={{
          WebkitMaskImage:
            "linear-gradient(to bottom, rgba(0, 0, 0, 1) 60%, rgba(0, 0, 0, 0) 100%)",
          maskImage:
            "linear-gradient(to bottom, rgba(0, 0, 0, 1) 60%, rgba(0, 0, 0, 0) 100%)",
        }}
      />
      <div className="px-4 flex flex-col gap-8 -mt-14 z-[19]">
        <div className="flex flex-col gap-4">
          <span className="text-[38px] font-bold">{props?.title}</span>
        </div>
        <div className="flex flex-col gap-4">
          {Beneficios.map((beneficio: string, index: number) => (
            <div key={index} className="flex gap-2 items-center">
              <span className="text-green-400 text-xl">
                <FaCheckCircle />
              </span>
              <span className="text-xl">{beneficio}</span>
            </div>
          ))}
        </div>
        <div className="flex items-center justify-center">
          <span className="text-[48px] font-bold">$ {props.pricing}</span>
        </div>
      </div>
    </div>
  );
};

export default Package;
