import Package from "./components/Packages";

const Membership = () => {
  return (
    <div className="grid gap-8 grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
      <Package
        image="/img/memberships/package-1.png"
        title="Basico"
        period="mensual"
        pricing={100}
      />
      <Package
        image="/img/memberships/package-2.png"
        title="Medio"
        period="trimestral"
        pricing={300}
      />
      <Package
        image="/img/memberships/package-3.png"
        title="Advance"
        period="semestral"
        pricing={500}
      />
      <Package
        image="/img/memberships/package-4.png"
        title="Pro"
        period="anual"
        pricing={1000}
      />
    </div>
  );
};

export default Membership;
